      }
      this.promedioPonderado =
        ((sum / this.notasParciales.length) * this.porcentajeNotas) / 100;

    this.Msg_Promedio_Ponderado = 'Promedio ponderado de '+ this.tipoEvaluacion + ' es '+ this.promedioPonderado;
